import { Component } from '@angular/core';
import { Router, NavigationEnd } from '@angular/router';
import { CategoryService } from '../../services/category.service';
import { MostWantedService } from '../../services/mostwanted.service';
import { CategoryModel } from '../../models/category.model';
declare var $: any;
@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent {
  categories: CategoryModel[] = new Array<CategoryModel>();
  mostWanted: any[] = new Array<any>();
  listCategory: any = [];
  slugCategory: string;
  idCity: any;
  searched = '';
  statusModal = 'close';
  statusModalCity = 'close';
  allCategories: any = [];
  emptyStatusCategory = 'close';
  emptyStatusCity = 'close';
  city: any = [
    {
      id: 1,
      name: 'São Paulo'
    },
    {
      id: 2,
      name: 'Rio de Janeiro'
    }
  ];
  allCity: any = [
    {
      id: 1,
      name: 'São Paulo'
    },
    {
      id: 2,
      name: 'Rio de Janeiro'
    }
  ];

  constructor(
    private router: Router,
    private categoryService: CategoryService,
    private mostWantedService: MostWantedService
  ) {
    $(document).ready(function () {
      $('.navbar-toggler').click(function (e) {
        e.preventDefault();
        if ($('.navbar-collapse').hasClass('opened')) {
          $('.navbar-collapse').removeClass('opened');
          $('.nav-backdrop').removeClass('show');
        } else {
          $('.navbar-collapse').addClass('opened');
          $('.nav-backdrop').addClass('show');
        }
      });
      if (window.innerWidth < 992) {
        $('.all-categories').slick({
          arrows: false,
          dots: true,
          infinite: true,
          slidesToShow: 2,
          slidesToScroll: 2,
          responsive: [{
            breakpoint: 552,
            settings: {
              slidesToShow: 1,
              slidesToScroll: 1,
            }
          }]
        });
        $('.other-results').slick({
          arrows: false,
          dots: true,
          slidesToScroll: 1,
          slidesToShow: 2,
          responsive: [{
            breakpoint: 552,
            settings: {
              slidesToShow: 1,
              slidesToScroll: 1,
              centerMode: true,
              centerPadding: '80px',
            }
          }]
        });
      }
    });
    this.gelAllCategories();
    this.gelMostWanted();

    this.router.events.subscribe((evt) => {
      if (!(evt instanceof NavigationEnd)) {
        return;
      }
      window.scrollTo(0, 0);
    });
  }

  findMatches(wordToMatch, category) {
    return category.filter(item => {
      const regex = new RegExp(wordToMatch, 'gi');
      return item.name.match(regex);
    });
  }

  filterRegexMatchAPI(value, category) {
    return this.findMatches(value, category);
  }

  changeCategory(e) {
    e.target.parentElement.setAttribute('data-value', e.target.value);
    const result = this.filterRegexMatchAPI(e.target.value, this.allCategories);
    result.length === 0 ? this.emptyStatusCategory = 'active' : this.emptyStatusCategory = 'close';
    this.listCategory = result;
  }
  changeCity(e) {
    e.target.parentElement.setAttribute('data-city', e.target.value);
    const result = this.filterRegexMatchAPI(e.target.value, this.allCity);
    result.length === 0 ? this.emptyStatusCity = 'active' : this.emptyStatusCity = 'close';
    this.city = result;
  }

  handleOpenModalCategory(e) {
    window.scrollTo(0, 0);
    const visor = e.target.parentElement.parentElement.querySelector('.form-visor');
    visor.classList.remove('show');
    this.statusModal = 'open';
  }

  handleOpenModalCity(e) {
    window.scrollTo(0, 0);
    const visor = e.target.parentElement.parentElement.querySelector('.form-visor');
    visor.classList.remove('show');
    this.statusModalCity = 'open';
  }

  closeModal() {
    this.statusModal = 'close';
  }

  closeModalCity() {
    this.statusModalCity = 'close';
  }

  handleCategoryModal(e) {
    const slugCategory = e.target.getAttribute('id');
    const textCategory = e.target.innerHTML;
    const visor = document.querySelector('.form-fields-category .form-visor');
    const inputVisuor = visor.querySelector('input');
    visor.setAttribute('data-value', textCategory);
    visor.setAttribute('data-click', slugCategory);
    inputVisuor.value = textCategory;
    this.statusModal = 'close';
  }

  handleCityModal(e) {
    const idCity = e.target.getAttribute('id');
    const textCity = e.target.innerHTML;
    const visor = document.querySelector('.form-fields-where .form-visor');
    const inputVisuor = visor.querySelector('input');
    visor.setAttribute('data-value', textCity);
    visor.setAttribute('data-click', idCity);
    inputVisuor.value = textCity;
    this.statusModalCity = 'close';
  }

  searchSpace() {
    this.searched = '';
    this.slugCategory = document.querySelector('[data-js="formFieldsCategory"]').getAttribute('data-click');
    this.idCity = document.querySelector('[data-js="formFieldsCity"]').getAttribute('data-city');
    this.validateSearch();
    if (this.slugCategory !== '0' && this.idCity != null) {
      this.router.navigate([`search/${this.slugCategory}`], {
        queryParams: {
          page: 1,
          per_page: 6,
          city: this.idCity
        }
      });
    }
  }

  searchByCategory(category: string) {
    this.router.navigate([`search/${category}`], {
      queryParams: {
        page: 1,
        per_page: 6
      }
    });
  }

  validateSearch() {
    if (this.slugCategory === '0' && this.idCity == null) {
      this.searched = 'all';
      return;
    }
    if (this.slugCategory === '0') {
      this.searched = 'category';
    }
    if (this.idCity == null) {
      this.searched = 'city';
    }
  }

  toogleFields(e) {
    this.searched = '';
    const valueInput = e.target.getAttribute('data-value');
    const valueInputCity = e.target.getAttribute('data-city');
    const elementHtml = e.target;
    const category = document.getElementById('category');
    const city = document.getElementById('city');
    this.returnToggledFields(elementHtml.id);
    if (elementHtml.classList.contains('show')) {
      elementHtml.classList.remove('show');
    } else {
      if (valueInput !== null && valueInput !== '') {
        this.listCategory = this.filterRegexMatchAPI(valueInput, this.allCategories);
      }
      if (valueInputCity !== null && valueInputCity !== '') {
        this.city = this.filterRegexMatchAPI(valueInputCity, this.allCity);
      }

      elementHtml.classList.add('show');
    }
  }

  returnToggledFields(id) {
    const category = document.getElementById('category');
    const city = document.getElementById('city');

    switch (id) {
      case 'categoryField':
        city.classList.remove('show');
        break;

      case 'cityField':
        category.classList.remove('show');
        break;

      case 'category':
        city.classList.remove('show');
        break;

      case 'city':
        category.classList.remove('show');
        break;

      default:
        break;
    }

  }

  toogleFieldsInput(e) {
    e.target.parentElement.classList.add('show');
    this.returnToggledFields(e.target.id);
    this.listCategory = this.filterRegexMatchAPI(e.target.value, this.allCategories);
  }

  toogleFieldsInputCity(e) {
    e.target.parentElement.classList.add('show');
    this.returnToggledFields(e.target.id);
    this.city = this.filterRegexMatchAPI(e.target.value, this.allCity);
  }

  handleClickType(e) {
    const textElement = e.target.innerText;
    const idElement = e.target.getAttribute('id');
    const elementVisor = e.target.parentElement.parentElement.firstChild;
    elementVisor.querySelector('input').value = textElement;
    elementVisor.setAttribute('data-click', idElement);
    elementVisor.setAttribute('data-value', textElement);
    elementVisor.classList.remove('show');
    elementVisor.classList.remove('showAll');
  }
  handleClickTypeCity(e) {
    const textElement = e.target.innerText;
    const idElement = e.target.getAttribute('id');
    const elementVisor = e.target.parentElement.parentElement.firstChild;
    elementVisor.querySelector('input').value = textElement;
    elementVisor.setAttribute('data-click', idElement);
    elementVisor.setAttribute('data-city', textElement);
    elementVisor.classList.remove('show');
    elementVisor.classList.remove('showAll');
  }

  async gelAllCategories() {
    try {
      const categories = await this.categoryService.getAllCategories();
      this.categories = categories;
      this.listCategory = categories;
      this.allCategories = categories;
    } catch (e) {
      console.log(e);
    }
  }

  async gelMostWanted() {
    try {
      const mostWanted = await this.mostWantedService.getMostWanted();
      this.mostWanted = mostWanted.spaces;

    } catch (e) {
      console.log(e);
    }
  }

  price(value) {
    if (value) {
      const valueFormat = value.toFixed(2).split('.');
      valueFormat[0] = `R$ ${valueFormat[0].split(/(?=(?:...)*$)/).join('.')}`;
      return `${valueFormat.join(',')}`;
    }
  }
}
